* {
    margin: 0;
    padding: 0;
}

body {
    font-size: 10px;
    font-family: 'Roboto Condensed';
}

section {
    margin: 3em 0em;
    padding: 3em;
}

.search-filed {
    width: 100%;
    height: 50px;
    border: none;
    border-bottom: 2px solid #eee;
    outline: none;
    font-size: 2em;
    font-weight: lighter;
    letter-spacing: 1.3px;
}

.search-filed:focus {
    border-bottom: 2px solid lightseagreen;
}

.section-heading {
    padding: 2em 0 1em;
    border-bottom: 1px solid #eee;
    margin-bottom: 1em;
}

.video-element {
    width: 250px;
    padding: 10px;
    vertical-align: top;
    display: inline-block;
    -webkit-box-shadow: 2px 3px 8px #eee;
            box-shadow: 2px 3px 8px #eee;
    margin: 10px;
}

.video-information p {
    font-size: 1.8em;
    text-combine-upright: none;
    font-weight: 400;
    text-rendering: auto;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

.video-information small {
    height: 70px;
    display: block;
    padding: 5px;
    overflow: hidden;
    font-size: 1.3em;
    font-weight: lighter;
    color: #999;
}

img {
    width: 100%;
}

.section-heading h2 {
    font-weight: 100;
    font-size: 5em;
    line-height: 1;
}

.block-offset {
    margin-left: 10em;
}

.User_list {
    max-width: 600px;
    margin: auto;
}

.User_item {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    padding: 5px;
    border-bottom: 1px solid grey;
    font-size: 1.4em;
}

.User_info {
    text-align: right;
}

.container {
    position: relative;
    display: block;
    width: 100%;
}

.Photo_container {
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    background: url(http://yourshot.nationalgeographic.com/u/fQYSUbVfts-T7odkrFJckdiFeHvab0GWOfzhj7tYdC0uglagsDNeZgL7slPxjZIiombl5-bW2HBqmzLhARbUNJsrRqjWV6IMFYQTIEzP6MKTY0DaknXmuusRLZQK0kLI_OZBklbFVkTdTM6DiWwEaAzWIQMaMX89UXOOIYE2dgSC23mc5NE4ICH7AIdrNiMgvK58QBLtenrbFQ9m5bGotDjysxnwgts/);
    background-size: cover;
}

.photo-element {
}

.photo-element:before {
}

.video-player {
    padding: 20px;
    background: black;
    display: -ms-flexbox;
    display: flex;
    text-align: center;
}

.video-iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

}

.video-description h2 {
    color: #fff;
    font-weight: 100;
    font-size: 3.2em;
}

.video-description {
    padding: 20px;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
}

.video-wrapper {
    -ms-flex-positive: 1;
    flex-grow: 1;
    padding: 15px;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    min-width: 50%;
    height: 0;
    position: relative;
    padding-bottom: 27.25%;
}

.video-description h3 {
    color: #888;
    font-weight: 100;
    font-size: 2.2em;
}



